import { Component } from "react";
import { Badge, Button, Card, Container, Dropdown, OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import { Link } from "react-router-dom";
import ProfileImage from "../CommonComponents/ProfileImage/ProfileImage";
import moment from "moment";
import { Loader } from "../CommonComponents/Loader";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import EmptyWithIcon from "../CommonComponents/Empty/EmptyWithIcon";
import { connect } from "react-redux";
import { deleteCollaborator, getCollaboratorList, updatePermission, updateStatus } from "../../../../actions/collaboratorActions";
import { GET_COLLABORATOR_LIST } from "../../../../reducer/keys";
import withRouter from "../../../routes/WithRouter";
import { SWAL_FIRE, TOAST_ERROR } from "../../../../utils";
import TabHeading from "../AccountInfo/TabHeading";

class Collaborator extends Component {

    state = {
        loading: true,
        userRole: null

    }

    componentDidMount() {
        const { userData } = this.props
        if (userData) this.getCollaborators(userData)
    }

    componentWillReceiveProps(nextProps) {
        const { userData } = nextProps
        if (JSON.stringify(userData) != JSON.stringify(this.props.userData)) this.getCollaborators(userData)
    }

    getCollaborators = (userData) => {
        const { setCollaboratorList } = this.props
        getCollaboratorList().then(response => {
            const userType = userData.collaborator == '' || userData.collaborator.activeAccount == 0 ? 3 : 0
            const { workspace } = response.data.responseData
            setCollaboratorList(workspace)
            this.setState({ loading: false, userRole: userType })
        }).catch(ex => { this.setState({ loading: false }, () => { }) })
    }

    modifyCollaborator = (value, type) => {
        const { updateCollaboratorPermission, deleteCollaborator } = this.props
        const commonContent = window?.APP_CONTENT?.["common"] ?? null;

        SWAL_FIRE({
            title: commonContent?.are_you_sure ?? "",
            text: type == "delete" ? commonContent?.delete_item ?? "" : commonContent?.modifying_setting ?? "",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: type == "delete" ? commonContent?.yes_delete ?? "" : commonContent?.yes_change ?? "",
            confirmButtonColor: type != "delete" ? `var(--bs-success)` : '',
            cancelButtonText: commonContent?.cancel ?? "",      
        }).then((result) => {
            if (result.isConfirmed) type == "delete" ? deleteCollaborator({ collaborators: String(value) }) : updateCollaboratorPermission(value)
        })
    }

    render() {
        const { loading, userRole } = this.state
        const { collaboratorList, userData } = this.props;
        const content = window?.APP_CONTENT ?? {};
        const { collab: pageContent, common: commonContent, sideBar } = content;
        
        return <TabHeading userData={userData} crumbs={[{ name: sideBar?.collaborator ?? "", link: "/home", icon: "grid-2", imgUrl: userData?.user?.picture }, { name: sideBar?.home ?? "", link: "/home" }, { name: sideBar?.collaborator ?? "" }]}>
            <Container className="container-collaborator">
                <Card>
                    <Card.Header className='f-between d-flex'>
                        <h2 className="me-2 h4 my-3">{pageContent?.title ?? ""}</h2>
                        <div className="f-center">
                            <Link to={'/collaborator/add'} className="btn btn-primary my-2 btn-sm" title={pageContent?.invite ?? ""}>
                                <FontAwesomeIcon icon={'plus'} />
                            {pageContent?.invite ?? ""}
                        </Link>
                    </div>
                </Card.Header>
                <Card.Body style={{ height: 'calc(100vh - 255px)', overflowY: 'auto' }}>
                    <div style={{ minHeight: 'calc(100vh - 287px)' }}>
                        {loading ? <Loader /> : collaboratorList.length > 0 ?
                            <div className="table-responsive">
                                <Table size='lg' className="align-middle gs-0 gy-3">
                                    <thead className="thead-light">
                                        <tr>
                                            {[{ name: commonContent?.collaborator ?? "", width: 160 }, { name: commonContent?.invited ?? "", width: 130 }, { name: commonContent?.status ?? "", width: 100 }, { name: commonContent?.permission ?? "", width: 100 }, { name: commonContent?.invited_by ?? "", width: 120 }, { name: commonContent?.action ?? "", width: 70 }].map((head, index) => { return <th key={index} className={`fw-bold text-nowrap ${head.name == "Actions" ? "text-end" : ""}`} style={{ minWidth: head.width }}>{head.name}</th> })}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {collaboratorList.map(collaborator => {
                                            return <tr>
                                                <td>
                                                    <div className="d-flex align-items-center">
                                                        <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                            imageSize={40}
                                                            symbolClass={'symbol-label fs-6 bg-primary'}
                                                            name={collaborator['UserCollab.name']} profileUrl={collaborator['UserCollab.picture']} networkId={''} />
                                                        <div className="overflow-hidden" style={{ maxWidth: 200 }}>
                                                            <p className="text-gray-dark fw-semibold mb-0 text-ellipsis fs-8">{collaborator['UserCollab.name']}</p>
                                                            <span className="fs-9 mb-0 text-gray-500 fw-medium text-ellipsis d-block">{collaborator['UserCollab.emailId']}</span>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td>
                                                    <p className="text-gray-900 fw-medium mb-0 text-nowrap">{moment(collaborator['UserCollab.created']).format("Do MMM YYYY")}</p>
                                                    <span className="fs-9 fw-medium text-gray-500 text-nowrap">{moment.utc(new Date(collaborator['UserCollab.created'])).format("h:mm a")}</span>
                                                </td>
                                                <td>
                                                    {collaborator.status != 2 ?
                                                        <label className="form-check form-switch">
                                                            <input className="form-check-input" type="checkbox" checked={collaborator.status == 1} onChange={() => this.props.updateStatus(collaborator)} />
                                                        </label>
                                                        :
                                                        <Badge bg="" className="badge-light-warning">{commonContent?.pending ?? ""}</Badge>
                                                    }
                                                </td>
                                                <td>
                                                    <div className="d-flex align-items-center">

                                                        {userRole == 3 ? <Dropdown style={{ minWidth: 200 }}>
                                                            <Dropdown.Toggle variant="secondary" className="d-flex align-items-center w-100 justify-content-between bg-white fs-9">
                                                                {collaborator.role == 1 ? commonContent?.admin ?? "" : commonContent?.manager ?? ""}
                                                            </Dropdown.Toggle>
                                                            <Dropdown.Menu className="w-100">
                                                                <Dropdown.Item key={0} onClick={() => collaborator.role == 1 ? TOAST_ERROR("Collaborator is already an admin.") : this.modifyCollaborator(collaborator, "change")}>{commonContent?.admin ?? ""}</Dropdown.Item>
                                                                <Dropdown.Item key={1} onClick={() => collaborator.role == 2 ? TOAST_ERROR("Collaborator is already a manager.") : this.props.navigate(`/collaborator/edit/${collaborator.id}?permission=1`)}>{commonContent?.manager ?? ""}</Dropdown.Item>
                                                            </Dropdown.Menu>
                                                        </Dropdown> : collaborator.role == 1 ? commonContent?.admin ?? "" : commonContent?.manager ?? ""}
                                                    </div>
                                                </td>
                                                <td>
                                                    <div className="d-flex align-items-center cursor-pointer text-decoration-none">
                                                        <ProfileImage className={'symbol symbol-36 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                            imageSize={36}
                                                            symbolClass={'symbol-label fs-6 bg-primary'}
                                                            name={collaborator['UserAdmin.name']} profileUrl={collaborator['UserAdmin.picture']} />
                                                        <p className="text-gray-dark fw-medium mb-0 text-ellipsis">{collaborator['UserAdmin.name']}</p>
                                                    </div>
                                                </td>
                                                <td className="pr-0 text-end">
                                                    <div className="d-flex align-items-center justify-content-end">
                                                        <OverlayTrigger placement="top" overlay={<Tooltip>{commonContent?.edit ?? ""}</Tooltip>}>
                                                            <Link to={`/collaborator/edit/${collaborator.id}`} id={`col-edit-${collaborator.id}`} className={`btn btn-icon btn-sm btn-active-primary ms-2`} title={commonContent?.edit ?? ""}>
                                                                <FontAwesomeIcon type="solid" icon={'pen'} />
                                                            </Link>
                                                        </OverlayTrigger>
                                                        <OverlayTrigger placement="top" overlay={<Tooltip>{commonContent?.remove ?? ""}</Tooltip>}>
                                                            <Button id={`col-remove-${collaborator.id}`} variant="icon" size='sm' className={`btn-active-danger ms-2`} aria-label="delete" onClick={() => this.modifyCollaborator(collaborator.id, "delete")}>
                                                                <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                            </Button>
                                                        </OverlayTrigger>
                                                    </div>
                                                </td>
                                            </tr>
                                        })}
                                    </tbody>
                                </Table>
                            </div>
                            :
                            <div className="f-center" style={{ minHeight: 'calc(100vh - 260px)' }}>
                                <EmptyWithIcon title={pageContent?.no_collab ?? ""} desc={pageContent?.no_collab_desc ?? ""} icon={'user-plus'} iconDark={'user-plus-dark'} button={{ text: pageContent?.invite ?? "", action: () => this.props.navigate('/collaborator/add') }} isFullScreen={false} />
                            </div>
                        }

                    </div>
                </Card.Body>
            </Card>
        </Container>
        </TabHeading>
    }
}

const mapStateToProps = state => {
    return {
        collaboratorList: state.collaborator.collaboratorList,
        userData: state.authorized?.userData?.user_data
    }
}

const mapDispatchToProps = dispatch => ({
    deleteCollaborator: (id) => dispatch(deleteCollaborator(id)),
    setCollaboratorList: (workspace) => dispatch({ type: GET_COLLABORATOR_LIST, payload: workspace }),
    updateCollaboratorPermission: (collaborator) => dispatch(updatePermission(collaborator)),
    updateStatus: (collaborator) => dispatch(updateStatus(collaborator))
})

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Collaborator))